.privacypage {
  padding: 30px;
}

.privacyPolicy {
  padding: 40px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.privacyPolicy h1 {
  font-size: 30px;
  font-weight: 600;
  color: var(--maincolor);
  margin-bottom: 20px;
}

.privacy_policy_description {
  margin-top: 20px;
  font-size: 16px;
  font-weight: 300;
  color: #000;
  line-height: 1.5;
  white-space: pre-line;
}

@media screen and (max-width: 768px) {
  .privacypage {
    padding: 10px;
  }
  .privacyPolicy {
    padding: 20px;
    width: 100%;
    overflow-wrap: break-word;
  }
  .privacyPolicy h1 {
    font-size: 20px;
  }
}
